
.reportlist th{
    background: linear-gradient(to bottom, #777777 0%,#444444 100%) !important;
    color: white;
    font-weight: normal;
    min-width: 100px;
}
.reportlist th:first-child {
    min-width: 200px;
    position: sticky;
    left: 0;
}
.reportlist th:last-child {
    min-width: 100px;
    position: sticky;
    right: 0;
}
.reportlist tr{
    background-color: #fff;
}
.reportlist tr.row-grey,
.reportlist td.cell-grey {
  background-color: #ececec;
}
.reportlisthead{
    font-size: 18px;
    margin-top: 5px;
    font-weight: 500;
    margin-bottom: 15px;
}
.reportlist{
    margin: 10px 0px 20px;
}
.reportlist table {
  border-collapse: collapse;
  width: 100%;
  font-size: 13px;
}
.reportlist td, th {
  /* border: 1px solid #ececec; */
  text-align: left;
  padding: 8px 7px;
  color: #333;
}
.reportlist td {
    width: 100px;
}
.reportlist td:first-child {
    width: 200px;
    position: sticky;
    left: 0;
    background-color: inherit;
}
.reportlist td:last-child {
    width: 100px;
    text-align: center;
    position: sticky;
    right: 0;
    background-color: inherit;
}
.cashinflowclick, .cashoutflowclick, .nonoperatflowclick{
    cursor: pointer;
    color: #2a7fff;
    border: 1px solid #2a7fff;
    padding: 5px;
    margin-left: 10px;
}
.reportbarchart{
    background: #f9f9f9;
    padding:20px;
    clear: both;
    margin: 20px 0px;
}
.highcharts-exporting-group{
    display: none;
}
.highcharts-credits{
    display: none;
}
.reportlist .editdltbtn{
	font-size: 12px;
	padding: 6px;
	box-shadow: none;
    border: none;
    outline: none;
    color: #007bff;
    cursor: pointer;
}

.reportlist .form-control {
    font-size: 13px;
}
.accordion__button {
    border: 1px solid #ececec;
}
.accordion__button:not(.no-arrow) table td:first-child:before {
    display: inline-block;
    content: '';
    height: 10px;
    width: 10px;
    margin-right: 12px;
    border-bottom: 2px solid currentColor;
    border-right: 2px solid currentColor;
    transform: rotate(-45deg);
    transition: .3s;
}

.accordion__heading {
    cursor: pointer;
}
/* accordion styles */
.accordion__button[aria-expanded='true']:not(.no-arrow) table td::before,
.accordion__button[aria-selected='true']:not(.no-arrow) table td::before {
    transform: rotate(45deg);
}

.accordion__panel {
    animation: fadein 0.35s ease-in;
}
@keyframes fadein {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.accordion__button[aria-expanded='true'] td .cashinflowclick.hide,
.accordion__button[aria-selected='true']:not(.no-arrow):not(.with-arrow-data) table td:first-child ~ *,
.accordion__button[aria-expanded='true']:not(.no-arrow):not(.with-arrow-data) table td:first-child ~ * {
    visibility: hidden;
}

@media(max-width: 1024px) {
    .reports .sidebar-expanded {
        width: 160px;
    }
}

#exportexceltable td:not(:first-child) {
    text-align:right;
}
.hidelast8 td:nth-child(n+10),
.hidelast8 th:nth-child(n+10),
.hidefirst8 td:nth-child(-n+10),
.hidefirst8 th:nth-child(-n+10) {
    display: none;
}

.hidefirst8 td:first-child,
.hidefirst8 th:first-child {
    display: table-cell;
}

#exporttable .col-md-12 {
    padding: 0;
}

.accordion__button:focus {
    outline: none;
    border: 1px solid #2a7fff;
}

.fixed-table {
    width: calc(100% - 630px);
    position: sticky;
    top: 0;
    overflow-x: auto;
    z-index: 9;
    -ms-overflow-style: none;
    border-bottom: 2px solid #ccc;
    scrollbar-width: none;
}

.fixed-table .reportlist {
    margin-bottom: 0;
    margin-top: 0;
}

.sidebar-collapsed + .pay-rec-wrap .fixed-table {
    width: calc(100% - 250px);
}

.sidebar-collapsed + .pay-rec-wrap .quickview-sidebar-expanded {
    width: calc(100% - 500px);
}

.sidebar-expanded + .pay-rec-wrap .quickview-sidebar-collapsed {
    width: calc(100% - 408px);
}

.pay-rec-wrap #quickview-sidebar .leftbaricon {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    left: 0;
    font-size: 12px;
    padding: 4px 0;
    z-index: 1;
}

.fixed-table::-webkit-scrollbar {
  display: none;
}

.visible-hidden {
    visibility: hidden;
}

.no-arrow,
.no-arrow:focus {
    border: none;
}

