.full-loader {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    background-color: rgba(255, 255, 255, 0.8);
}

.pos-relative {
    position: relative;
}

.reports-wrapper {
    min-width: 600px;
}

.report-main-wrapper {
    height: 70.5vh;
    overflow: hidden;
    overflow-y: auto;
    padding-right: 10px;
    margin-bottom: 10px;
}

#quickview-sidebar #sideTabBar li {
    padding: 8px 7px;
    font-size: 13px;
}
#quickview-sidebar #showDate {
    font-weight: 500;
}

#quickview-sidebar #pLisitng .card {
    border: 1px solid #ddd;
}

#quickview-sidebar .mData {
    font-size: 13px;
}

.invoices-wrapper {
    height: 66vh;
    overflow-y: auto;
}

#share-report {
    font-size: 14px;
    border: none;
}
#email-list {
    border: 1px solid #ced4da;
    border-radius: 5px;
}
#email-list .autocomplete i {
    display: none;
}
#email-list .autocomplete input {
    border: none;
    padding: 0;
    height: auto;
}
#email-list .autocomplete input::placeholder {
  color: #495057;
}
#email-list .autocomplete input:focus {
    border: none;
    box-shadow: none;
}
#email-list .autocomplete .suggestions,
#email-list .autocomplete .no-suggestions {
    top: 27px;
}
#message {
    font-size: 13px;
    color: #495057;
}
#share-report:focus,
#message:focus {
    box-shadow: none;
}

.reports .card-body {
    padding-right: 10px;
}

.collapsed-quickview {
    flex: 0 0 4%;
    max-width: 4%;
}

.expanded-report {
    flex: 0 0 94.666667%;
    max-width: 94.666667%;
    padding-left: 0;
    padding-right: 20px;
}

.collapsed-quickview .onlyicon {
    padding: 15px;
}

.collapsed-quickview .leftbaricon {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    left: -14px;
    font-size: 12px;
    padding: 4px 0;
}

.report-notes-container {
    display: flex;
    flex-direction: column;
    height: 70vh;
}

.report-notes-wrapper,
.add-notes-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.add-notes-wrapper textarea,
.textarea-wraper textarea {
    height: 180px;
    margin-bottom: 10px;
    font-size: 14px;
}
.add-notes-wrapper {
    flex: 1 1 45%;
}
.report-notes-wrapper {
    flex:1 1 55%;
    overflow-y: auto;
}
.add-notes-wrapper .button-wrap button {
    width: 49%;
    display: inline-block;
    margin: 0 0 0 auto;
    font-size: 14px;
}
.username {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.note {
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
    position: relative;
}

.note .username,
.note .date {
    padding-left: 0;
    padding-right: 0;
    font-size: 12px;
}

.note .username i > i,
.note .date i > i {
    color: #a9a9a9;
    margin-right: 2px;
}
.note:hover .option-overlay {
    display: flex;
}
.option-overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .3);
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    border-radius: 5px;
    display: none;
    align-items: center;
    justify-content: center;
    flex-direction: row;
}

.option-overlay button {
    width: 30px;
    height: 30px;
    padding: 0;
    text-align: center;
    border-radius: 100%;
    background-color: #fff;
}

.option-overlay button i {
    font-size: 12px;
}

#sideTabBar li i {
    font-size: 18px;
}

.add-note-btn {
    width: 25px;
    margin: 0 0 0 auto;
}
.custom-editor-style .ck-editor__top {
    width: 100%;
    position: absolute;
    bottom: 1px;
}
.custom-editor-style .ck-editor__main {
    height: 350px;
}
.custom-editor-style  .ck-editor__editable_inline {
    height: 310px;
    min-height: 310px;
    overflow-y: auto;
}
.custom-editor-style .ck-editor__top .ck-sticky-panel .ck-toolbar {
    border-bottom-width: 1px;
}

@media(max-width: 1220px) {
    .expanded-report {
        flex: 0 0 92.5%;
        max-width: 92.5%;
    }
    .collapsed-quickview {
        flex: 0 0 5.5%;
        max-width: 5.5%;
    }
    #quickview-sidebar #nextDate,
    #quickview-sidebar #prevDate,
    #quickview-sidebar #showDate{
        font-size: 13px;
    }
    #quickview-sidebar #nextDate,
    #quickview-sidebar #prevDate {
        padding: 0 5px;
    }
    .invoices-wrapper {
        height: 67.5vh;
    }
    #quickview-sidebar .mData,
    .note-text {
        font-size: 10px;
    }
    #quickview-sidebar .card-body {
        padding-left: 10px;
    }
    #quickview-sidebar #sideTabBar li {
        font-size: 10px;
    }
    .add-notes-wrapper textarea,
    .textarea-wraper textarea {
        height: 100px;
        padding: 5px;
        font-size: 10px;
    }
    .note .username,
    .note .date {
        font-size: 7px;
    }
    .note .date {
        padding-left: 5px;
    }
}
